@import url('https://fonts.googleapis.com/css?family=Montserrat:400,800');

.logFormTitle {
  font-weight: bold;
  margin-bottom: 30px;
}

.logFormText {
  max-width: 430px;
  font-size: 24px;
  font-weight: 400;
  line-height: 40px;
  letter-spacing: 0.5px;
  margin: 20px 0 30px;
  color: #fff;
}

.logFormSpan {
  font-size: 12px;
}

.logFormText2 {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

.logFormButtonSubmit {
  border-radius: 20px;
  border: 2px solid #ff4b2b;
  background-color: #ff4b2b;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
  margin-top: 8px;
  transition: all 0.5s;
}

.logFormButtonSubmit:active {
  transform: scale(0.95);
}

.logFormButtonSubmit:focus {
  outline: none;
}

.logFormButtonSubmit:hover {
  transform: scale(1.05);
  cursor: pointer;
  background-color: #fff;
  color: #ff416c;
}

.logFormButton {
  border-radius: 20px;
  border: 2px solid #ffffff;
  background-color: transparent;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
  margin-top: 8px;
  transition: all 0.5s;
}

.logFormButton:active {
  transform: scale(0.95);
}

.logFormButton:focus {
  outline: none;
}

.logFormButton:hover {
  transform: scale(1.05);
  cursor: pointer;
  background-color: #fff;
  color: #ff416c;
}

.logForm {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

.logFormInput {
  background-color: #cdcdcde8;
  border: none;
  padding: 18px 15px;
  margin: 12px 0;
  width: 410px;
  font-size: 20px;
  outline: none;
}

.logFormInput:focus-visible {
  border: 2px solid #000000;
}
.container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 75vw;
  max-width: 100%;
  min-height: 480px;
  height: 62vh;
  margin: 300px auto 150px auto;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

.sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.container.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.container.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

.overlay {
  background: #ff416c;
  background: -webkit-linear-gradient(to right, #ff4b2b, #ff416c);
  background: linear-gradient(to right, #ff4b2b, #ff416c);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
  transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;

  transform: translateX(-50%);
  transition: transform 0.6s ease-in-out;
}

.overlay-left {
  transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
  transform: translateX(-50%);
  left: 25%;
}

.overlay-right {
  right: -27%;
  transform: translateX(-40%);
}

.container.right-panel-active .overlay-right {
  transform: translateX(20%);
}

.social-container {
  margin: 20px 0;
}

.social-container a {
  border: 1px solid #dddddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}

.logForm button:disabled {
  background-color: #b6b6b6;
  border: none;
  cursor: not-allowed;
}
.logForm button:disabled:hover {
  color: #fff;
}

.checkboxConteiner {
  position: relative;
}

.checkbox {
  position: absolute;
  appearance: revert;
  cursor: pointer;
  right: 20px;
  top: 35px;
}

input[type='checkbox']::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  transform: translate(-50%, -50%);
  background-color: #cdcdcde8;
  background-image: url(../../images/icons/eye-off.svg);
}

input[type='checkbox']:checked::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  transform: translate(-50%, -50%);
  background-color: #cdcdcde8;
  background-image: url(../../images/icons/eye.svg);

  visibility: visible;
}

.inputError {
  border: 2px solid red;
}

.errorMessage {
  color: red;
}